                      <tr class="siItem" v-if="skill.skill1 != ''"><p style="margin: 5px 0 5px" v-html="skill.skill1"></p></tr>
                      <tr class="siItem" v-if="skill.skill2 != ''"><p style="margin: 5px 0 5px" v-html="skill.skill2"></p></tr>
                      <tr class="siItem" v-if="skill.burst != ''"><p style="margin: 5px 0 5px" v-html="skill.burst"></p></tr>
                      <tr><p/></tr>
                    </table>
                  </div>
                </table>
              </tr>
            </table>
            <p/>
            <table>
              <tr>
                <div style="text-align: left"><strong>本機能の仕様</strong></div>
                <div style="text-align: left">
                  <ul>
                    <li>SR/RLは最大チャージで計算</li>
                    <li>RLの爆風による巻き込みは計算の対象外</li>
                    <li>SGの通常攻撃は全弾命中で計算　※10ペレットならダメージ表記は1/10</li>
                    <li>取り消し線が付いたスキルは無効状態　例）クリティカルがOFFの時のクリダメupバフ</li>
                    <li>味方ステータス依存のバフはアタッカー自身の数値で計算　例）ギルティ</li>
                    <li>最大HPやスキル発動者依存のスキルはアタッカーの詳細設定の攻撃力から一定の係数でステータスを算出して計算　例）2B、ナガ</li>
                    <li>フルバースト補正が適用されるスキルはフルバーストOFFでもフルバーストで計算　例）ブリッド、2B</li>
                    <li>ダメージ計算式の"その他"はどのスキルが該当するか不明なため未使用</li>
                  </ul>
                </div>
                <p/>
                <div style="text-align: left"><strong>敵防御力について</strong></div>
                <div style="text-align: left">
                  <ul>
                    <li>射撃場はボスが140で雑魚が100、特殊個体は13125</li>
                    <li>ユニオンレイドLv10ボスは6000～10000（2023年9月時点）</li>
                    <li>大体において防御関連は誤差レベルなため余程硬い敵でなければ数値変更は不要と思われる</li>
                  </ul>
                </div>
                <p/>
                <div style="text-align: left"><strong>注意事項</strong></div>
                <div style="text-align: left">
                  <ul>
                    <li>ゲーム上で不可能な組合せの制限はしきれないため詳細設定や使用スキルの内容は要確認
                      <br>例：紅蓮バーストなどの全体攻撃にコアヒットや適性距離を適用できるが、実際には適用されない。
                      <br>例：リターとミランダのバーストバフを同時に適用できるが、実際にはバーストⅠ同士なので不可能。
                    </li>
                    <li>ゲームの仕様を全て把握しているものではないため結果の正しさは自身で確認する事　※ダメージ欄ダブルクリックで計算式を表示</li>
                  </ul>
                </div>
              </tr>
            </table>
            <p/>
            <table>
              <tr class="note note-smart">
                画像の著作権はSHIFT UP CORP.に帰属しています。
              </tr>
            </table>
          </td>
        </table>
