                  <div style="text-align: left"><strong>自動認識の使い方</strong></div>
                  <div style="text-align: left">
                    <ul>
                      <li>起動はNIKKE画面指定→ウィンドウタブ→NIKKE(最小化していると選択できないので注意)</li>
                      <li>停止はブラウザとは別にポップアップされている共有の停止、又は、本サイト自体を更新</li>
                      <li>認識した文字列が全く違う場合はNIKKEの画面を拡大縮小したり移動させたりすると正しく読める場合がある</li>
                      <li>選択肢の文字列が短いものは認識が上手くいかない可能性高</li>
                      <li>NIKKEの画面が裏に隠れていたり最小化されていると読み取れない</li>
                    </ul>
                  </div>
                </tr>
                <tr v-if="tsImageAuto" :class="{'coDisplayNone' : tsImageAuto == 0}"><p/></tr>
                <tr>
                  <div style="text-align: left"><strong>画像/自動認識の仕様</strong></div>
                  <div style="text-align: left">
                    <ul>
                      <li>データはサーバへ送信せずローカルで処理</li>
                      <li>スマホ及び縦21:9モードには未対応</li>
                      <li>画像/自動認識を使用しないのであればOFFにした方が処理が早い</li>
                      <li>画像認識(補整)をONにすると処理に時間が掛かるが読めなかった選択肢が読めるようになる可能性がある</li>
                    </ul>
                  </div>
                </tr>
                <tr><p/></tr>
                <tr>
                  <div style="text-align: left"><strong>注意事項および使い方</strong></div>
                  <div style="text-align: left">
