                      <li>面談候補にいないニケは検索不可。面談データが揃うのを待つ事</li>
                      <li>*(半角アスタリスク)で検索すると全ての面談データを検索</li>
                    </ul>
                  </div>
                </tr>
              </table>
            </tr>
