      await axios.get(divisionsURL).then(function(response) {_this.divisions = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(contentsURL).then(function(response) {_this.contents = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(rewardURL).then(function(response) {_this.reward = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(experienceURL).then(function(response) {_this.experience = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(packsURL).then(function(response) {_this.packs = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(nikkesURL).then(function(response) {_this.nikkes = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(talkURL).then(function(response) {_this.talks = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(updatesURL).then(function(response) {_this.updates = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      //const endTime = performance.now();

      //ニケ一覧を加工
      _this.nikkes.forEach(function(item){
          item.skill1Infos.forEach(function(info){
            item.skill1 += "\n" + info.note.replace("{%1}", info.lvs[9].value); // スキル効果内の置換対象をLv10の値に置き換え
          });
          item.skill2Infos.forEach(function(info){
            item.skill2 += "\n" + info.note.replace("{%1}", info.lvs[9].value);
          });
          item.burstInfos.forEach(function(info){
            item.burst += "\n" + info.note.replace("{%1}", info.lvs[9].value);
          });
      });

      // 面談データを加工
      _this.talks.forEach(function(nikke){
        nikke.cases.forEach(function(caseItem){ // 選択肢1～2
          caseItem.trigramify = _this.trigramify(caseItem.select); // マッチング用データ
        });
      });

      // ニケ一覧を名前順でソートし、IDを振りなおし
      //const comp = (a, b) => [
      //    a.weapon > b.weapon,
      //    a.class > b.class
      //].reduce((x, y) => x << 1 | y);
      
      //_this.nikkes.forEach(function(nikke){
      //  nikke.allSort = String(nikke.burstclassSort).padStart(3, "0")
      //                 + String(nikke.rearitySort).padStart(3, "0")
      //                 + String(nikke.elementalSort).padStart(3, "0")
      //                 + String(nikke.weaponSort).padStart(3, "0")
      //                 + String(nikke.name).padStart(3, "0");
      //});
      //_this.nikkes.sort((a,b) => {
      //  if (a.allSort > b.allSort) { return 1 }
      //  if (a.allSort < b.allSort) { return -1 }
      //});
      
      //let nikkeId = 1;
      //let nikkeIdStr = String(nikkeId).padStart(3, "0");
      //_this.nikkes.forEach(function(nikke){
        //console.log(nikke.id + "->" + nikkeIdStr);
      //  nikke.id = "nikke" + nikkeIdStr;
      //  nikkeId++;
      //  nikkeIdStr = String(nikkeId).padStart(3, "0");
      //});

      // 名前順でソートしたニケ一覧を作成
      _this.nikkes_sort_name = _this.nikkes.concat();
      _this.nikkes_sort_name.sort((a,b) => {
        if (a.name > b.name) { return 1 }
        if (a.name < b.name) { return -1 }
      });

      //let set = 1;
      //this.sort_asc ? (set = 1) : (set = -1)
      //this.packs.sort((a, b) => {
      //  if (a[this.sort_key2] > b[this.sort_key2]) return -1;
      //  if (a[this.sort_key2] < b[this.sort_key2]) return 1;
        //if (a[this.sort_key1] < b[this.sort_key1]) return -1 * set;
        //if (a[this.sort_key1] > b[this.sort_key1]) return 1 * set;
      //  return 0;
      //});

      // クッキーからニケ並び順の設定を読み込み
      let idx = 0;
      _this.coNikkeSortKey.forEach(function(nikkeSort){
        if(localStorage.getItem('coNikkeSortKey['+idx+']', 1) != null){
          nikkeSort.SortNum = localStorage.getItem('coNikkeSortKey['+idx+']', 1);
        };
        ++idx;
      });
      _this.coNikkeSortKey.sort((a,b) => {
        if (a.SortNum > b.SortNum) { return 1 }
        if (a.SortNum < b.SortNum) { return -1 }
      });

      // ニケ一覧を並び替え
      _this.nikkes.forEach(function(nikke){
