    // 面談サポート画面キャプチャ
    let capture =  document.querySelector("[contenteditable]");
    capture.addEventListener("paste", function(e){
      let captureImg = document.querySelector("#tsOutputImage");
      // 画像の場合e.clipboardData.types.length == 0 かつ e.clipboardData.types[0] == "Files"となっているのでそれ以外を弾く
      if (!e.clipboardData || !e.clipboardData.types || (e.clipboardData.types.length != 1) || (e.clipboardData.types[0] != "Files")) return true;
      // ファイルとして得る(なぜかgetAsStringでは上手くいかなかった)
      let imageFile = e.clipboardData.items[0].getAsFile();
      // FileReaderで読み込む
      let fr = new FileReader();
      fr.onload = function(e) {
        // onload内ではe.target.resultにbase64が入っているのであとは煮るなり焼くなり
        let base64 = e.target.result;
        captureImg.src = base64;
        capture.childNodes.forEach(function(child){
          child.height = "100"
        });

        let nh = capture.childNodes[0].naturalHeight;
        let nw = capture.childNodes[0].naturalWidth;
        let sx = Math.round(nw/2 - (400/2)*(nh/983));
        let dx = Math.round(400 * nh/983);
        let sy1 = Math.round(nh * 640/983);
        let sy2 = Math.round(nh * (640+75)/983);
        let dy = Math.round(nh * 55/983);

        let captureCanvas1 = document.getElementById('tsOutputCanvas1');
        let captureContext1 = captureCanvas1.getContext('2d',{ willReadFrequently: true });
        captureContext1.drawImage(captureImg, sx, sy1, dx, dy, 0, 0, 410, 55);
        let buf = document.querySelector('#tsOutputCanvas1');
        Tesseract.recognize(buf, 'jpn',
            { logger: function(m) { document.querySelector('#tsOcrProgressManu').textContent = "log:1-" + m.status; } }
        )
        //Tesseract.recognize(buf, lng,
        //    { logger: function(m) { document.querySelector('#tsOcrProgressManu').textContent = "log:1-" + m.status; } }
        //)
        .then(function(result){
            let buf = result.data.text;
            buf = buf.replace(/\s+/g, ''); // スペース削除
            _this.tsSearchWordCls = 1;
            _this.tsSearchWord1 = buf;
        });

        let captureCanvas2 = document.getElementById('tsOutputCanvas2');
        let captureContext2 = captureCanvas2.getContext('2d',{ willReadFrequently: true });
        captureContext2.drawImage(captureImg, sx, sy2, dx, dy, 0, 0, 410, 55);
        buf = document.querySelector('#tsOutputCanvas2');
        Tesseract.recognize(buf, 'jpn',
            { logger: function(m) { document.querySelector('#tsOcrProgressManu').textContent = "log:2-" + m.status; } }
        )
        .then(function(result){
            let buf = result.data.text;
            buf = buf.replace(/\s+/g, ''); // スペース削除
            _this.tsSearchWordCls = 1;
            _this.tsSearchWord2 = buf;
        });

        if(_this.tsImageCorr == 1) {
          let imageData1 = captureContext1.getImageData(1, 1, 410, 55);
          let xmin = 10000;
          let xmax = 0;
          let ymin = 10000;
          let ymax = 0;
          for(let x = 0; x < 410; x=x+5) {
            for(let y = 0; y < 55; y=y+5) {
              var r = imageData1.data[(x + y*410)*4];
              if(r >= 100){
                if(xmin > x) xmin = x;
                if(xmax < x) xmax = x;
                if(ymin > y) ymin = y;
                if(ymax < y) ymax = y;
              }
            };
          };
          let sxn = sx+xmin-15;
          let syn = sy1+ymin-5;
          let dxn = (xmax-xmin)+30;
          let dyn = (ymax-ymin)+10;
          if(dyn < 25) dyn = 25;
          if(dxn > dyn/55*410) dyn = Math.round(dxn/410*55);
          if(dxn < dyn/55*410) dxn = Math.round(dyn/55*410);
          let captureCanvas1s = document.getElementById('tsOutputCanvas1s');
          let captureContext1s = captureCanvas1s.getContext('2d',{ willReadFrequently: true });
          captureContext1s.drawImage(captureImg, sxn, syn, dxn, dyn, 0, 0, 410, 55);
          buf = document.querySelector('#tsOutputCanvas1s');
          Tesseract.recognize(buf, 'jpn',
              { logger: function(m) { document.querySelector('#tsOcrProgressManu').textContent = "log:1s-" + m.status; } }
          )
          .then(function(result){
              let buf = result.data.text;
              buf = buf.replace(/\s+/g, ''); // スペース削除
              _this.tsSearchWordCls = 1;
              _this.tsSearchWord1s = buf;
          });

          let imageData2 = captureContext2.getImageData(1, 1, 410, 55);
          xmin = 10000;
          xmax = 0;
          ymin = 10000;
          ymax = 0;
          for(let x = 0; x < 410; x=x+5) {
            for(let y = 0; y < 55; y=y+5) {
              var r = imageData2.data[(x + y*410)*4];
              if(r >= 100){
                if(xmin > x) xmin = x;
                if(xmax < x) xmax = x;
                if(ymin > y) ymin = y;
                if(ymax < y) ymax = y;
              }
            };
          };
          sxn = sx+xmin-15;
          syn = sy2+ymin-5;
          dxn = (xmax-xmin)+30;
          dyn = (ymax-ymin)+10;
          if(dyn < 25) dyn = 25;
          if(dxn > dyn/55*410) dyn = Math.round(dxn/410*55);
          if(dxn < dyn/55*410) dxn = Math.round(dyn/55*410);
          let captureCanvas2s = document.getElementById('tsOutputCanvas2s');
          let captureContext2s = captureCanvas2s.getContext('2d',{ willReadFrequently: true });
          captureContext2s.drawImage(captureImg, sxn, syn, dxn, dyn, 0, 0, 410, 55);
          buf = document.querySelector('#tsOutputCanvas2s');
          Tesseract.recognize(buf, 'jpn',
              { logger: function(m) { document.querySelector('#tsOcrProgressManu').textContent = "log:2s-" + m.status; } }
          )
          .then(function(result){
              let buf = result.data.text;
              buf = buf.replace(/\s+/g, ''); // スペース削除
              _this.tsSearchWordCls = 1;
              _this.tsSearchWord2s = buf;
          });
        } else {
          _this.tsSearchWord1s = "";
          _this.tsSearchWord2s = "";
        };
      };
      fr.readAsDataURL(imageFile);
    });

