    mSsPackCheck(id, chkOn) { // スキル：課金パック選択
      let _this = this;

      _this.packs.forEach(function(pack){
        if(pack.id == id) {
          pack.check = pack.check * (-1);
          if (chkOn) {
            if (_this.selected[pack.id] === undefined) {
              _this.selected[pack.id] = true;
            } else {
              _this.selected[pack.id] = !_this.selected[pack.id];
            }
          }
          
          if(pack.items.ManualSkill1 >= 1)      _this.ssPackItems[0].BuyNum  += pack.items.ManualSkill1 * pack.check;
          if(pack.items.ManualSkill2 >= 1)      _this.ssPackItems[1].BuyNum  += pack.items.ManualSkill2 * pack.check;
          if(pack.items.ManualSkill3 >= 1)      _this.ssPackItems[2].BuyNum  += pack.items.ManualSkill3 * pack.check;
          if(pack.items.ManualBurst1 >= 1)      _this.ssPackItems[3].BuyNum  += pack.items.ManualBurst1 * pack.check;
          if(pack.items.ManualBurst2 >= 1)      _this.ssPackItems[4].BuyNum  += pack.items.ManualBurst2 * pack.check;
          if(pack.items.ManualBurst3 >= 1)      _this.ssPackItems[5].BuyNum  += pack.items.ManualBurst3 * pack.check;
