    mSsSetSkillInfo(idx) { // スキル：スキル詳細設定
      let _this = this;
      let repStr = "";
      
      _this.ssSkillInfos[idx].Disp[0] = "";
      _this.ssSkillInfos[idx].Disp[1] = "";
      _this.ssSkillInfos[idx].Disp[2] = "";

      _this.nikkes.forEach(function(nikke){
        if(nikke.id == _this.ssSkillSelectIds[idx]) {
          _this.ssSkillInfos[idx].Disp[0] = "スキル1：" + nikke.skill1Ct;
          _this.ssSkillInfos[idx].Disp[1] = "スキル2：" + nikke.skill2Ct;
          _this.ssSkillInfos[idx].Disp[2] = "バースト：" + nikke.burstCt;

          nikke.skill1Infos.forEach(function(info){
            _this.ssSkillInfos[idx].Disp[0] += "<br>" + info.note;
            if(info.note.indexOf('{%1}')) {
              if(Number(_this.ssSkill[idx].Goal[0]) > Number(_this.ssSkill[idx].Now[0])) {
                repStr = "<mark><strong>" + info.lvs[_this.ssSkill[idx].Now[0]-1].value + "→" + info.lvs[_this.ssSkill[idx].Goal[0]-1].value + "</strong></mark>";
              } else if(Number(_this.ssSkill[idx].Goal[0]) == Number(_this.ssSkill[idx].Now[0])) {
                repStr = "<mark><strong>" + info.lvs[_this.ssSkill[idx].Now[0]-1].value + "</strong></mark>";
              } else {
                repStr = "<mark style='background-color: #ff7f7f;'><strong>" + info.lvs[_this.ssSkill[idx].Now[0]-1].value + "→" + info.lvs[_this.ssSkill[idx].Goal[0]-1].value + "</strong></mark>";
              };
              _this.ssSkillInfos[idx].Disp[0] = _this.ssSkillInfos[idx].Disp[0].replace('{%1}', repStr);
            };
          });

          nikke.skill2Infos.forEach(function(info){
            _this.ssSkillInfos[idx].Disp[1] += "<br>" + info.note;
            if (info.note.indexOf('{%1}')) {
              if(Number(_this.ssSkill[idx].Goal[1]) > Number(_this.ssSkill[idx].Now[1])) {
                repStr = "<mark><strong>" + info.lvs[_this.ssSkill[idx].Now[1]-1].value + "→" + info.lvs[_this.ssSkill[idx].Goal[1]-1].value + "</strong></mark>";
              } else if(Number(_this.ssSkill[idx].Goal[1]) == Number(_this.ssSkill[idx].Now[1])) {
                repStr = "<mark><strong>" + info.lvs[_this.ssSkill[idx].Now[1]-1].value + "</strong></mark>";
              } else {
                repStr = "<mark style='background-color: #ff7f7f;'><strong>" + info.lvs[_this.ssSkill[idx].Now[1]-1].value + "→" + info.lvs[_this.ssSkill[idx].Goal[1]-1].value + "</strong></mark>";
              };
              _this.ssSkillInfos[idx].Disp[1] = _this.ssSkillInfos[idx].Disp[1].replace('{%1}', repStr);
            };
          });

          nikke.burstInfos.forEach(function(info){
            _this.ssSkillInfos[idx].Disp[2] += "<br>" + info.note;
            if (info.note.indexOf('{%1}')) {
              if(Number(_this.ssSkill[idx].Goal[2]) > Number(_this.ssSkill[idx].Now[2])) {
                repStr = "<mark><strong>" + info.lvs[_this.ssSkill[idx].Now[2]-1].value + "→" + info.lvs[_this.ssSkill[idx].Goal[2]-1].value + "</strong></mark>";
              } else if(Number(_this.ssSkill[idx].Goal[2]) == Number(_this.ssSkill[idx].Now[2])) {
                repStr = "<mark><strong>" + info.lvs[_this.ssSkill[idx].Now[2]-1].value + "</strong></mark>";
              } else {
                repStr = "<mark style='background-color: #ff7f7f;'><strong>" + info.lvs[_this.ssSkill[idx].Now[2]-1].value + "→" + info.lvs[_this.ssSkill[idx].Goal[2]-1].value + "</strong></mark>";
              };
              _this.ssSkillInfos[idx].Disp[2] = _this.ssSkillInfos[idx].Disp[2].replace('{%1}', repStr);
            };
          });
        };
      });

      // 画面へ値反映するための処理
      for(let i=0; i<_this.ssSkillInfos.length; ++i) {
        _this.$set(_this.ssSkillInfos, i, _this.ssSkillInfos[i]);
      };
