    mTsCaptureStart() { // 面談サポート：画面キャプチャ開始
      let _this = this;
      //let takeScreenshotsButton = document.querySelector("#take-screenshots");
      let videoContainer = document.querySelector("div#capture-stream");

      Cap();
      async function Cap() {
        _this.tsCaptureStream = await navigator.mediaDevices.getDisplayMedia({audio: false, video: true});
        videoContainer.innerHTML = '';
        if (_this.tsCaptureStream) {
          const video = document.createElement("video");
          video.autoplay = true;
          video.srcObject = _this.tsCaptureStream;
          videoContainer.appendChild(video);
          //takeScreenshotsButton.removeAttribute("disabled")

          if(_this.tsCaptureInterval != null) {
            clearInterval(_this.tsCaptureInterval);
            _this.tsCaptureInterval = null;
          };
          _this.mTsTakeScreenshot();
        };
      };
    },

    mTsTakeScreenshot() { // 面談サポート：スクリーンショット
      let _this = this;
      let interTime = _this.tsCaptureIntervalTime * 1000;


      _this.tsCaptureInterval = setInterval(function() {
        ScreenShot();
        async function ScreenShot() {
          _this.tsCaptureSkipSec -= Number(_this.tsCaptureIntervalTime);

          if(_this.tsCaptureSkipSec <= 0) {

            const videoTrack = _this.tsCaptureStream.getVideoTracks()[0];
            const imageCapture = new ImageCapture(videoTrack);
            let imageBitmap = null;
            try {
              imageBitmap = await imageCapture.grabFrame();

              //canvasContainer.innerHTML = '';
              const canvas = document.createElement("canvas");
              canvas.width = imageBitmap.width;
              canvas.height = imageBitmap.height;

              let sx = Math.round(canvas.width/2 - (400/2)*(canvas.height/983));
              let dx = Math.round(400 * canvas.height/983);
              let sy1 = Math.round(canvas.height * 640/983);
              let sy2 = Math.round(canvas.height * (640+75)/983);
              let dy = Math.round(canvas.height * 55/983);
            /*
              let captureCanvas1 = document.getElementById('tsOutputCanvas1');
              captureCanvas1.style.height = dy + "px";
              captureCanvas1.style.width = dx + "px";
              let captureContext1 = captureCanvas1.getContext('2d');
              captureContext1.drawImage(imageBitmap, sx, sy1, dx, dy, 0, 0, 410, 55);
              let buf1 = document.querySelector('#tsOutputCanvas1');
              Tesseract.recognize(buf1, 'jpn',
                  { logger: function(m) { document.querySelector('#tsOcrProgressAuto').textContent = "log:1-" + m.status; } }
              )
              .then(function(result){
                  let buf = result.data.text;
                  buf = buf.replace(/\s+/g, ''); // スペース削除
                  _this.tsSearchWordCls = 1;
                  _this.tsSearchWord1 = buf;
              });

              let captureCanvas2 = document.getElementById('tsOutputCanvas2');
              captureCanvas2.style.height = dy + "px";
              captureCanvas2.style.width = dx + "px";
              let captureContext2 = captureCanvas2.getContext('2d');
              captureContext2.drawImage(imageBitmap, sx, sy2, dx, dy, 0, 0, 410, 55);
              let buf2 = document.querySelector('#tsOutputCanvas2');
              Tesseract.recognize(buf2, 'jpn',
                  { logger: function(m) { document.querySelector('#tsOcrProgressAuto').textContent = "log:2-" + m.status; } }
              )
              .then(function(result){
                  let buf = result.data.text;
                  buf = buf.replace(/\s+/g, ''); // スペース削除
                  _this.tsSearchWordCls = 1;
                  _this.tsSearchWord2 = buf;
              });
            */
              // 上選択肢の切り出し画像を作成
              let captureCanvas1 = document.getElementById('tsOutputCanvas1');
              let captureContext1 = captureCanvas1.getContext('2d',{ willReadFrequently: true });
              captureContext1.drawImage(imageBitmap, sx, sy1, dx, dy, 0, 0, 410, 55);

              // 下選択肢の切り出し画像を作成
              let captureCanvas2 = document.getElementById('tsOutputCanvas2');
              let captureContext2 = captureCanvas2.getContext('2d',{ willReadFrequently: true });
              captureContext2.drawImage(imageBitmap, sx, sy2, dx, dy, 0, 0, 410, 55);

              // 面談画面であるかチェック（特定の複数ピクセルのr要素の最大を取り出し）
              let imageCheck = captureContext1.getImageData(1, 1, 410, 55);
              let rsum = 0;
              for(let x = 3; x <= 7; x++) {
                for(let y = 3; y <= 7; y++) {
                  var rc = imageCheck.data[(x + x*410)*4];
                  rsum += rc;
                };
              };

              // 面談画面（チェック箇所の背景が黒）の場合のみ処理
              if(rsum < 300) {
                let buf = document.querySelector('#tsOutputCanvas1');
                Tesseract.recognize(buf, 'jpn',
                    { logger: function(m) { document.querySelector('#tsOcrProgressAuto').textContent = "log:1-" + m.status; } }
                )
                .then(function(result){
                    let buf = result.data.text;
                    buf = buf.replace(/\s+/g, ''); // スペース削除
                    _this.tsSearchWordCls = 1;
                    _this.tsSearchWord1 = buf;
                });
                
                buf = document.querySelector('#tsOutputCanvas2');
                Tesseract.recognize(buf, 'jpn',
                    { logger: function(m) { document.querySelector('#tsOcrProgressAuto').textContent = "log:1-" + m.status; } }
                )
                .then(function(result){
                    let buf = result.data.text;
                    buf = buf.replace(/\s+/g, ''); // スペース削除
                    _this.tsSearchWordCls = 1;
                    _this.tsSearchWord2 = buf;
                });

                if(_this.tsImageCorr == 1) {
                  // 上選択肢の補正画像を作成
                  let imageData1 = captureContext1.getImageData(1, 1, 410, 55);
                  let xmin = 10000;
                  let xmax = 0;
                  let ymin = 10000;
                  let ymax = 0;
                  for(let x = 0; x < 410; x=x+5) {
                    for(let y = 0; y < 55; y=y+5) {
                      var r = imageData1.data[(x + y*410)*4];
                      if(r >= 100){
                        if(xmin > x) xmin = x;
                        if(xmax < x) xmax = x;
                        if(ymin > y) ymin = y;
                        if(ymax < y) ymax = y;
                      }
                    };
                  };
                  let sxn = sx+xmin-15;
                  let syn = sy1+ymin-5;
                  let dxn = (xmax-xmin)+30;
                  let dyn = (ymax-ymin)+10;
                  if(dyn < 25) dyn = 25;
                  if(dxn > dyn/55*410) dyn = Math.round(dxn/410*55);
                  if(dxn < dyn/55*410) dxn = Math.round(dyn/55*410);
                  let captureCanvas1s = document.getElementById('tsOutputCanvas1s');
                  let captureContext1s = captureCanvas1s.getContext('2d',{ willReadFrequently: true });
                  captureContext1s.drawImage(imageBitmap, sxn, syn, dxn, dyn, 0, 0, 410, 55);
                  buf = document.querySelector('#tsOutputCanvas1s');
                  Tesseract.recognize(buf, 'jpn',
                      { logger: function(m) { document.querySelector('#tsOcrProgressAuto').textContent = "log:1s-" + m.status; } }
                  )
                  .then(function(result){
                      let buf = result.data.text;
                      buf = buf.replace(/\s+/g, ''); // スペース削除
                      _this.tsSearchWordCls = 1;
                      _this.tsSearchWord1s = buf;
                  });

                  // 下選択肢の補正画像を作成
                  let imageData2 = captureContext2.getImageData(1, 1, 410, 55);
                  xmin = 10000;
                  xmax = 0;
                  ymin = 10000;
                  ymax = 0;
                  for(let x = 0; x < 410; x=x+5) {
                    for(let y = 0; y < 55; y=y+5) {
                      var r = imageData2.data[(x + y*410)*4];
                      if(r >= 100){
                        if(xmin > x) xmin = x;
                        if(xmax < x) xmax = x;
                        if(ymin > y) ymin = y;
                        if(ymax < y) ymax = y;
                      }
                    };
                  };
                  sxn = sx+xmin-15;
                  syn = sy2+ymin-5;
                  dxn = (xmax-xmin)+30;
                  dyn = (ymax-ymin)+10;
                  if(dyn < 25) dyn = 25;
                  if(dxn > dyn/55*410) dyn = Math.round(dxn/410*55);
                  if(dxn < dyn/55*410) dxn = Math.round(dyn/55*410);
                  let captureCanvas2s = document.getElementById('tsOutputCanvas2s');
                  let captureContext2s = captureCanvas2s.getContext('2d',{ willReadFrequently: true });
                  captureContext2s.drawImage(imageBitmap, sxn, syn, dxn, dyn, 0, 0, 410, 55);
                  buf = document.querySelector('#tsOutputCanvas2s');
                  Tesseract.recognize(buf, 'jpn',
                      { logger: function(m) { document.querySelector('#tsOcrProgressAuto').textContent = "log:2s-" + m.status; } }
                  )
                  .then(function(result){
                      let buf = result.data.text;
                      buf = buf.replace(/\s+/g, ''); // スペース削除
                      _this.tsSearchWordCls = 1;
                      _this.tsSearchWord2s = buf;
                  });

                } else {
                  document.querySelector('#tsOcrProgressAuto').textContent = "Select Window not found...";
                };

              } else {
                _this.tsSearchWord1s = "";
                _this.tsSearchWord2s = "";
              };

            } catch(e) {
              clearInterval(_this.tsCaptureInterval);
              _this.tsCaptureInterval = null;
            };
          } else {
            document.querySelector('#tsOcrProgressAuto').textContent = "Found!";
          };
        };
      }, interTime);
    },

