    mTsSetOneWord(word) { // 面談サポート：検索ワード追加
      let _this = this;
      _this.tsSearchWordCls = 2;
      if (word == "AC") {
        _this.tsSearchWord1 = "";
        _this.tsSearchWord2 = "";
        _this.tsSearchWord1s = "";
        _this.tsSearchWord2s = "";

      } else if (word == "C") {
        if(_this.tsSearchWord1.length >= 1) {
          _this.tsSearchWord1 = _this.tsSearchWord1.slice(0, _this.tsSearchWord1.length-1)
        };
      
      } else if (word == "\"" && _this.tsSearchWord1 != "") {
        let lastWord = _this.tsSearchWord1.slice(-1);
        let newWord = "";
        if(lastWord == "う") newWord = "ゔ";
        else if(lastWord == "か") newWord = "が";
        else if(lastWord == "き") newWord = "ぎ";
        else if(lastWord == "く") newWord = "ぐ";
        else if(lastWord == "け") newWord = "げ";
        else if(lastWord == "こ") newWord = "ご";
        else if(lastWord == "さ") newWord = "ざ";
        else if(lastWord == "し") newWord = "じ";
        else if(lastWord == "す") newWord = "ず";
        else if(lastWord == "せ") newWord = "ぜ";
        else if(lastWord == "そ") newWord = "ぞ";
        else if(lastWord == "た") newWord = "だ";
        else if(lastWord == "ち") newWord = "ぢ";
        else if(lastWord == "つ") newWord = "づ";
        else if(lastWord == "て") newWord = "ぜ";
        else if(lastWord == "と") newWord = "ぞ";
        else if(lastWord == "は") newWord = "ば";
        else if(lastWord == "ひ") newWord = "び";
        else if(lastWord == "ふ") newWord = "ぶ";
        else if(lastWord == "へ") newWord = "べ";
        else if(lastWord == "ほ") newWord = "ぼ";
        else if(lastWord == "ぱ") newWord = "ば";
        else if(lastWord == "ぴ") newWord = "び";
        else if(lastWord == "ぷ") newWord = "ぶ";
        else if(lastWord == "ぺ") newWord = "べ";
        else if(lastWord == "ぽ") newWord = "ぼ";
        if(newWord != "") _this.tsSearchWord1 = _this.tsSearchWord1.slice(0, _this.tsSearchWord1.length-1) + newWord;

      } else if (word == "゜" && _this.tsSearchWord1 != "") {
        let lastWord = _this.tsSearchWord1.slice(-1);
        let newWord = "";
        if(lastWord == "は") newWord = "ぱ";
        else if(lastWord == "ひ") newWord = "ぴ";
        else if(lastWord == "ふ") newWord = "ぷ";
        else if(lastWord == "へ") newWord = "ぺ";
        else if(lastWord == "ほ") newWord = "ぽ";
        else if(lastWord == "ば") newWord = "ぱ";
        else if(lastWord == "び") newWord = "ぴ";
        else if(lastWord == "ぶ") newWord = "ぷ";
        else if(lastWord == "べ") newWord = "ぺ";
        else if(lastWord == "ぼ") newWord = "ぽ";
        if(newWord != "") _this.tsSearchWord1 = _this.tsSearchWord1.slice(0, _this.tsSearchWord1.length-1) + newWord;

      } else {
        _this.tsSearchWord1 += word;
      };
    },

