    mSkillInfoClose(skillId, nikkeId) { // 部隊編成 - スキル詳細を消す     
      // 絞り込み対象スキル情報の配列から×ボタンが押されたものを消す
      let cnt = 0;
      let idx = 0;
      this.skillInfos.forEach(function(skillInfo){
        if(skillInfo.id == skillId) {
          //alert("remove:" + cnt)
          idx = cnt;
        }
        cnt++;
      });
      this.skillInfos.splice(idx, 1);

      // 部隊欄に編成済みのニケではない場合、画像を非表示化
      if (this.coSelTab == 3) {
        if (!document.getElementById(nikkeId).parentNode.id.startsWith('squad')) {
          document.getElementById(nikkeId).style.display = "none";
        };
      } else {
        document.getElementById(nikkeId).style.display = "none";
      }
    },

    mSetQfFilter(flgCls) { // 部隊編成：絞り込み
      let _this = this;

      // 検索キーワード配列を作成
      let keyword = [];
      let target = [];
      let reg = new RegExp("　",'g');
      let str = '';
      keyword = _this.qfFil_FreeWord.trim().replace(reg, " ").split(" ");
      if (_this.qfFil_S_OH_OWN) str += " 自分(?!.*、)(?!.*味方)(?!.*敵)(?!.*対象).*に|自分と" // "自分"で絞り込んでいる場合はキーワード扱いとする
      if (_this.qfFil_S_OH_ALLY) str += " 味方(?!.*、)(?!.*自分)(?!.*敵)(?!.*対象).*に|味方(?!.*、)(?!.*自分)(?!.*敵)(?!.*対象).*と" // "味方"で絞り込んでいる場合はキーワード扱いとする
      if (_this.qfFil_S_OH_ENE) str += " 敵(?!.*、)(?!.*自分)(?!.*味方)(?!.*対象).*に|敵(?!.*、)(?!.*自分)(?!.*味方)(?!.*対象).*と" // "敵"で絞り込んでいる場合はキーワード扱いとする
      if (_this.qfFil_S_OH_TAG) str += " 対象(?!.*、)(?!.*自分)(?!.*味方)(?!.*敵).*に|対象(?!.*、)(?!.*自分)(?!.*味方)(?!.*敵).*と" // "対象"で絞り込んでいる場合はキーワード扱いとする
      target = str.trim().replace(reg, " ").split(" ");

      _this.skillInfos.splice(0,1000); // 絞り込み対象スキル情報をリセット

      _this.nikkes.forEach(function(nikke){
        let disp = '';

        if(_this.qfFil_B_1 || _this.qfFil_B_2 || _this.qfFil_B_3) {
          if(!_this.qfFil_B_1 && nikke.burstclass == 'Ⅰ') disp = 'none';
          if(!_this.qfFil_B_2 && nikke.burstclass == 'Ⅱ') disp = 'none';
          if(!_this.qfFil_B_3 && nikke.burstclass == 'Ⅲ') disp = 'none';
        }
        
        if(_this.qfFil_C_Atk || _this.qfFil_C_Def || _this.qfFil_C_Sup) {
          if(!_this.qfFil_C_Atk && nikke.class == '火力型') disp = 'none';
          if(!_this.qfFil_C_Def && nikke.class == '防御型') disp = 'none';
          if(!_this.qfFil_C_Sup && nikke.class == '支援型') disp = 'none';
        }

        if(_this.qfFil_E_Fir || _this.qfFil_E_Wat || _this.qfFil_E_Win || _this.qfFil_E_Ele || _this.qfFil_E_Iro) {
          if(!_this.qfFil_E_Fir && nikke.elemental == '灼熱') disp = 'none';
          if(!_this.qfFil_E_Wat && nikke.elemental == '水冷') disp = 'none';
          if(!_this.qfFil_E_Win && nikke.elemental == '風圧') disp = 'none';
          if(!_this.qfFil_E_Ele && nikke.elemental == '電撃') disp = 'none';
          if(!_this.qfFil_E_Iro && nikke.elemental == '鉄甲') disp = 'none';
        }

        if(_this.qfFil_W_AR || _this.qfFil_W_SMG || _this.qfFil_W_SG || _this.qfFil_W_SR || _this.qfFil_W_RL || _this.qfFil_W_MG) {
          if(!_this.qfFil_W_AR && nikke.weapon == 'AR') disp = 'none';
          if(!_this.qfFil_W_SMG && nikke.weapon == 'SMG') disp = 'none';
          if(!_this.qfFil_W_SG && nikke.weapon == 'SG') disp = 'none';
          if(!_this.qfFil_W_SR && nikke.weapon == 'SR') disp = 'none';
          if(!_this.qfFil_W_RL && nikke.weapon == 'RL') disp = 'none';
          if(!_this.qfFil_W_MG && nikke.weapon == 'MG') disp = 'none';
