                  flgFind = true;
                }
                if((reg.test(nikke.burst)) && (burst.includes('<mark>'))) {
                  burst = burst.replace(reg, '<mark>$1</mark>');
                  flgFind = true;
                }
              }
            };
          };
        
          if(flgFind) {
            let reg = /(<mark>)/i;
            if(!reg.test(skill1)) skill1 = "";
            if(!reg.test(skill2)) skill2 = "";
            if(!reg.test(burst)) burst = "";
            //alert(_this.skillInfos.length)
            _this.skillInfos.push({ id: _this.skillInfos.length, nikkeId: nikke.id, name: skillInfoName, skill1: skill1.replace(regRet, "<br>"), skill2: skill2.replace(regRet, "<br>"), burst: burst.replace(regRet, "<br>") });
          } else {
            disp = 'none';
          }

        } else if (((keyword.length >= 2 || keyword[0] != '') || (target.length >= 2 || target[0] != '')) && (disp != 'none')) {
          let flgFindKey = false;
          let flgFindTag = false;
          let regRet = /\n/g;
          let skillInfoName = nikke.name+"【バースト"+nikke.burstclass+"／"+nikke.class+"／"+nikke.weapon+"／"+nikke.elemental+"】";
          let skill1 = nikke.skill1;
          let skill2 = nikke.skill2;
          let burst  = nikke.burst;

          for (let cnt = 0; cnt < keyword.length; cnt++) {
            let reg = new RegExp("(" + keyword[cnt] + ")");
            if (reg.test(nikke.skill1)) {
              skill1 = skill1.replace(reg, '<mark>$1</mark>');
              flgFindKey = true;
            }
            if (reg.test(nikke.skill2)) {
              skill2 = skill2.replace(reg, '<mark>$1</mark>');
              flgFindKey = true;
            }
            if (reg.test(nikke.burst)) {
              burst = burst.replace(reg, '<mark>$1</mark>');
              flgFindKey = true;
            }
          }
          if (keyword.length == 0) {
            flgFindKey = true;
          }

          for (let cnt = 0; cnt < target.length; cnt++) {
            let reg = new RegExp("(" + target[cnt] + ")");
            if (reg.test(nikke.skill1)) {
              skill1 = skill1.replace(reg, '<mark>$1</mark>');
              flgFindTag = true;
            }
            if (reg.test(nikke.skill2)) {
              skill2 = skill2.replace(reg, '<mark>$1</mark>');
              flgFindTag = true;
            }
            if (reg.test(nikke.burst)) {
              burst = burst.replace(reg, '<mark>$1</mark>');
              flgFindTag = true;
            }
          }
          if (target.length == 0) {
            flgFindTag = true;
          }
          
          if(flgFindKey && flgFindTag) {
            let reg = /(<mark>)/i;
            if(!reg.test(skill1)) skill1 = "";
            if(!reg.test(skill2)) skill2 = "";
            if(!reg.test(burst)) burst = "";
            _this.skillInfos.push({ id: _this.skillInfos.length, nikkeId: nikke.id, name: skillInfoName, skill1: skill1.replace(regRet, "<br>"), skill2: skill2.replace(regRet, "<br>"), burst: burst.replace(regRet, "<br>") });
          } else {
            disp = 'none';
          }
        }

        if (_this.coSelTab == 4) {
          if (document.getElementById("sm"+nikke.id).parentNode.id == 'smGraph') disp = ''; // グラフに配置されたニケは強制表示
          document.getElementById("sm"+nikke.id).style.display = disp;
        } else if (_this.coSelTab == 3) {
          if (document.getElementById("sf"+nikke.id).parentNode.id.startsWith('squad')) disp = ''; // 部隊欄に配置されたニケは強制表示
          document.getElementById("sf"+nikke.id).style.display = disp;
        } else if (_this.coSelTab == 6) {
          //if (document.getElementById("fsImgAttacker"+nikke.id).parentNode.parentNode.parentNode.id.startsWith('fsAttacker')) disp = ''; // アタッカー欄に配置されたニケは強制表示
          //if (document.getElementById("fsImgSupporter"+nikke.id).parentNode.parentNode.parentNode.id.startsWith('fsSupporter')) disp = ''; // サポーター欄に配置されたニケは強制表示
          document.getElementById("fs"+nikke.id).style.display = disp;
        } else {
          _this.tsTalkNikkes.forEach(function(talk){
            if(talk.name == nikke.name) {
              if(talk.check) disp = true; // 面談候補上でチェック状態なら強制表示
              document.getElementById("tsNikke"+talk.id).style.display = disp;
            };
          });
        };
      })
